.post {
  width: 100%;
  /* width: 500px; */
  border-radius: 10px;
  -webkit-box-shadow: 0px 0px 16px -8px rgba(0, 0, 0, 0.68);
  box-shadow: 0px 0px 16px -8px rgba(0, 0, 0, 0.68);
  margin: 30px 0;
}
.postWrapper {
  padding: 10px;
  
}

.postTop{
    display:flex;
    align-items:center;
    justify-content: space-between;
}
.postTopLeft {
  display: flex;
  align-items: center;
}
.postProfileImg {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  object-fit: cover;
}
.postUserName {
  font-size: 15px;
  font-weight: 500;
  margin: 0 10px;
  text-decoration: none !important;
  color: inherit !important;
  
}

.postDate{
    font-size: 12px;
}
.postCenter{
    margin: 20px 0;
}

.postImg{
    margin-top: 20px;
    width: 100%;
    max-height: 500px;
    object-fit: contain;
}

.postBottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.postBottomLeft{
    display: flex;
    align-items: center;   
}

.likeIcon{
    width: 24px;
    height: 24px;
    margin-right: 5px;
    color: #FF6347;
    cursor: pointer;
}
.likeIcon:hover{
    color:#1877f2;
}
.postLikeCounter{
    font-size: 14px;
}

.postCommentText{
    cursor: pointer;
    font-size: 15px;
}